<template >
  <v-container fluid>
    <template justify="center" align="center" v-for="i in 8">
      <v-row no-gutters justify="center" :key="i" class="mb-3">
        <template v-for="j in 8">
          <v-btn
            v-on:click.self="toggleBtn"
            :key="j"
            value="false"
            color="primary"
            x-large
            class="note"
            v-bind:index="i"
          >
          </v-btn>
        </template>
      </v-row>
    </template>
  </v-container>
</template>

<script>
export default {
  name: "MusicPad",

  data: () => ({
    alignments: ["start", "center", "end"]
  }),

  methods: {
    // Toggle music pad value and color on/off
    toggleBtn: function (event) {
      let buttonIndex = event.target.attributes[4].value;
      let buttonValue = event.target.value;
      event.target.value = buttonValue === "false";

      if (event.target.value === "true") {
        event.target.style.backgroundColor = "green";
      } else {
        event.target.style.backgroundColor = "#f5f5f5";
      }

      this.$musicPad.playAudio(buttonIndex, buttonValue);
    },
  },
};
</script>

<style scoped>
.mb-3 {
  margin-bottom: 10px;
}

.note {
  height: 8vw !important;
  width: 8vw !important;
  padding-top: 5px;
  padding-bottom: 5px;
  background-color: #f5f5f5;
  text-align: center;
  cursor: pointer;
}

.row {
  align-items: center;
  justify-content: center;
}
</style>