<template>
  <v-bottom-navigation
    v-model="value"
    :background-color="color"
    dark
    grow
    fixed
  >
    <v-btn large>
      <span>Play</span>

      <v-icon>mdi-play</v-icon>
    </v-btn>

    <v-btn large>
      <span>Stop</span>

      <v-icon>mdi-stop</v-icon>
    </v-btn>

    <v-btn large>
      <span>Record</span>

      <v-icon>mdi-record</v-icon>
    </v-btn>
  </v-bottom-navigation>
</template>

<script>
  export default {
    name: 'MusicPadMenu',

    data: () => ({ value: 1 }),

    computed: {
      color () {
        switch (this.value) {
          case 0: return '#4CAF50'
          case 1: return '#212121'
          case 2: return '#F44336'
          default: return 'red'
        }
      },
    },
  }
</script>


<style scoped>

.v-item-group.v-bottom-navigation {
  top: 0;
}

</style>