export default class MusicPad {
	constructor(scale, waveShape) {
		this.scale = scale;
		this.waveShape = waveShape;
		this.chunks = [];
		this.AudioContext = window.AudioContext || window.webkitAudioContext;
		this.context = new AudioContext();
		this.song = this.context.createMediaStreamDestination();
        this.mediaRecorder = new MediaRecorder(this.song.stream);
        this.notes = []

        this.setNoteFrequencies(this.scale)
    }
    
    setNoteFrequencies(scale) {
        switch (scale) {
            case 'aMin':
                this.notes = [220.0, 246.9, 261.6, 293.7, 329.6, 349.2, 392.0, 440.0]
                break;
            case 'cMaj':
                this.notes = [261.63, 293.66, 329.63, 349.23, 392.0, 440.0, 493.88, 523.25]
                break;
            case 'gMaj':
                this.notes = [196.0, 220.0, 246.94, 261.63, 293.66, 329.63, 369.99, 392.0]
                break;
            default:
                break;
        }
    }

	boop() {
		console.log("boop");
	}

	playAudio(buttonIndex, buttonValue) {
        if (buttonValue === 'true') {
            return;
        }
        
		var osc = this.context.createOscillator();
		var gain = this.context.createGain();
		osc.connect(gain);
		gain.connect(this.context.destination);
		gain.connect(this.song);

		gain.gain.linearRampToValueAtTime(1, this.context.currentTime + 0.005);
		gain.gain.linearRampToValueAtTime(0, this.context.currentTime + 0.4);

        buttonIndex -= 1;
		try {
            osc.frequency.value = this.notes[buttonIndex];
            osc.start();
        } catch (error) {
            console.log(`${buttonIndex} was not found in notes`)
        }
	}
}
